<script>
  import { onMount } from "svelte";
  let activeTab = "all";

  const tabs = [
    { label: "Tất cả", value: "all" },
    { label: "Tài liệu", value: "docx" },
    { label: "Hợp đồng", value: "pdf" },
    { label: "Báo cáo", value: "xlsx" },
  ];

  const fileHistory = [
    {
      date: "Hôm nay - 03/05/2025",
      files: [
        {
          name: "Báo cáo doanh thu.docx",
          uploader: "Hùng",
          time: "10:32 AM",
          type: "docx",
        },
        {
          name: "Thống kê quý.xlsx",
          uploader: "Lan",
          time: "09:15 AM",
          type: "xlsx",
        },
        {
          name: "Chi phí tháng 4.pdf",
          uploader: "Linh",
          time: "08:05 AM",
          type: "pdf",
        },
      ],
    },
    {
      date: "Hôm qua - 02/05/2025",
      files: [
        {
          name: "Hợp đồng mẫu.pdf",
          uploader: "Minh",
          time: "17:40",
          type: "pdf",
        },
        {
          name: "Kế hoạch marketing.xlsx",
          uploader: "Tú",
          time: "16:22",
          type: "xlsx",
        },
      ],
    },
  ];

  const names = [
    "Báo cáo",
    "Thống kê",
    "Chi phí",
    "Hợp đồng",
    "Kế hoạch",
    "Phiếu thu",
    "Biên bản",
    "Ngân sách",
    "Bảng lương",
    "Quyết toán",
  ];
  const extensions = ["docx", "xlsx", "pdf"];
  const uploaders = [
    "Hùng",
    "Lan",
    "Linh",
    "Minh",
    "Tú",
    "Hoa",
    "Nam",
    "Thảo",
    "An",
    "Dũng",
  ];

function randomTime() {
  const h = String(Math.floor(Math.random() * 24)).padStart(2, "0");
  const m = String(Math.floor(Math.random() * 60)).padStart(2, "0");
  return `${h}:${m}`;
}

  function randomFile() {
 const ext = extensions[Math.floor(Math.random() * extensions.length)];
    const name = `${names[Math.floor(Math.random() * names.length)]} ${Math.floor(Math.random() * 100)}.${ext}`;
    return {
      name,
      uploader: uploaders[Math.floor(Math.random() * uploaders.length)],
      time: randomTime(),
      type: ext,
    };
  }

  function formatDate(dayOffset) {
    const d = new Date(2025, 4, 3); // 03/05/2025
    d.setDate(d.getDate() - dayOffset);
    const dd = String(d.getDate()).padStart(2, "0");
    const mm = String(d.getMonth() + 1).padStart(2, "0");
    const yyyy = d.getFullYear();
    const label =
      dayOffset === 0
        ? "Hôm nay"
        : dayOffset === 1
          ? "Hôm qua"
          : `Ngày ${dd}/${mm}/${yyyy}`;
    return `${label} - ${dd}/${mm}/${yyyy}`;
  }

  onMount(() => {
    //const fileHistory = [];

    // Tạo 100 ngày, mỗi ngày có 1–4 files
    for (let i = 0; i < 100; i++) {
      const fileCount = Math.floor(Math.random() * 4) + 1;
      const files = Array.from({ length: fileCount }, () => randomFile());
      fileHistory.push({
        date: formatDate(i),
        files,
      });
    }
  });

  function getIcon(type) {
    switch (type) {
      case "docx":
        return "📄";
      case "xlsx":
        return "📊";
      case "pdf":
        return "📕";
      default:
        return "📁";
    }
  }

  function getColorClass(type) {
    switch (type) {
      case "docx":
        return "text-blue-600";
      case "xlsx":
        return "text-green-600";
      case "pdf":
        return "text-red-600";
      default:
        return "text-gray-500";
    }
  }

  // Hàm lọc danh sách theo tab đang chọn
  function filterFiles(files) {
    if (activeTab === "all") return files;
    return files.filter((f) => f.type === activeTab);
  }
</script>

<div class="w-full h-full mx-auto p-6 space-y-8 items-center-safe">
  <!-- Tabs -->
  <div class="flex gap-4 border-b pb-2">
    {#each tabs as tab}
      <button
        on:click={() => (activeTab = tab.value)}
        class="px-4 py-2 text-sm font-medium border-b-2 transition-colors duration-200"
        class:border-blue-500={activeTab === tab.value}
        class:text-blue-600={activeTab === tab.value}
        class:text-gray-500={activeTab !== tab.value}
        class:border-transparent={activeTab !== tab.value}
      >
        {tab.label}
      </button>
    {/each}
  </div>

  <!-- File History -->
  {#each fileHistory as group (group.date)}
    {#if filterFiles(group.files).length > 0}
      <div>
        <h2 class="text-lg font-bold text-gray-700 mb-3">
          {group.date}
        </h2>
        <ul class="space-y-3">
          {#each filterFiles(group.files) as file (file.name)}
            <li
              class="flex items-center gap-4 p-4 bg-white shadow rounded-md hover:bg-gray-50 transition"
            >
              <div class={`text-xl ${getColorClass(file.type)}`}>
                {getIcon(file.type)}
              </div>
              <div class="flex-1">
                <p class="font-medium text-gray-800">
                  {file.name}
                </p>
                <p class="text-sm text-gray-500">
                  Tải lên bởi {file.uploader} • {file.time}
                </p>
              </div>
              <span class="text-xs text-gray-400">.{file.type}</span>
            </li>
          {/each}
        </ul>
      </div>
    {/if}
  {/each}
</div>
