<script>
  import { onMount } from 'svelte';
  import { mapTabDetail } from '$lib/mapper';
  import { autoRegister, loadComponentByName, preloadComponents } from '$lib/componentLoader';

  export let tabs = [];
  export let activeTabId;
  let currentComponent = null;

  let ready = false;

  $: if (ready && activeTabId) {
    doLoad(activeTabId);
  }

  const doLoad = async (kind) => {
    const name = mapTabDetail[kind] || activeTabId;
    currentComponent = await loadComponentByName(name);
  };

  onMount(async () => {
    autoRegister();
    await preloadComponents(['HomeDetail', 'ResultDetail']); // preload trước
    ready = true;
  });
</script>

<slot {currentComponent} {tabs} {activeTabId} {...$$restProps} />
