// Hàm map dữ liệu raw từ backend sang data cho Info.svelte
export function mapRecordToInfo(raw) {
    return {
        id: raw.id_iuid ?? null,
        code: raw.code ?? "",
        partner_name: raw.partner_name ?? "",
        partner_code: raw.partner_code ?? "",
        partner_address: raw.partner_address ?? "",
        partner_phone: raw.partner_phone ?? "",
        result_datetime: raw.result_datetime ?? null,
        result_date: raw.result_date ?? null,
        diagnosis: raw.diagnosis ?? "",
        findings: raw.findings ?? "",
        impression: raw.impression ?? "",
        recommendation: raw.recommendation ?? "",
        note: raw.note ?? "",
        item_name: raw.item_name ?? "",
        provider_name: raw.provider_name ?? "",
        provider_phone: raw.provider_phone ?? "",
        provider_email: raw.provider_email ?? ""
    };
}

// Hàm map ngược lại, từ data trong Info.svelte gửi về backend
export function mapInfoToRecord(data) {
    return {
        id_iuid: data.id,
        code: data.code,
        partner_name: data.partner_name,
        partner_code: data.partner_code,
        partner_address: data.partner_address,
        partner_phone: data.partner_phone,
        result_datetime: data.result_datetime,
        result_date: data.result_date,
        diagnosis: data.diagnosis,
        findings: data.findings,
        impression: data.impression,
        recommendation: data.recommendation,
        note: data.note,
        item_name: data.item_name,
        provider_name: data.provider_name,
        provider_phone: data.provider_phone,
        provider_email: data.provider_email
    };
}

