<script>
  import { onMount, tick, onDestroy } from 'svelte';
  import Portal from 'svelte-portal';
  import Icon from '@components/Icon.svelte';
  import LayoutK2 from '@components/layout/k2/index.js';
  import { download } from '$lib/utils.js';

  // import Modal from '@components/ui/Modal2.svelte';

  export let value = undefined;
  let showPopup = false;
  let buttonRef;
  let popupRef;
  let popupStyle = '';

  let open = false;

  async function togglePopup() {
    showPopup = !showPopup;
    if (showPopup) {
      await tick(); // Đảm bảo popup đã render vào DOM
      positionPopup();
    }
  }

  // Tính toán vị trí popup khi hiển thị
  async function positionPopup() {
    if (!buttonRef || !popupRef) return;
    // Lấy tọa độ và kích thước của nút
    const btnRect = buttonRef.getBoundingClientRect();
    const popupRect = popupRef.getBoundingClientRect();
    let top = btnRect.bottom + window.scrollY;
    let left = btnRect.left + window.scrollX;

    // Điều chỉnh nếu popup bị tràn ra ngoài màn hình ngang
    if (left + popupRect.width > window.innerWidth) {
      left = window.innerWidth - popupRect.width - 8; // 8px padding
    }
    // Điều chỉnh nếu popup bị tràn ra ngoài màn hình dọc
    if (top + popupRect.height > window.innerHeight + window.scrollY) {
      top = btnRect.top + window.scrollY - popupRect.height;
    }

    popupStyle = `
      position: absolute;
      top: ${top}px;
      left: ${left}px;
      z-index: 9999;
    `;
  }

  // Ẩn popup khi click ra ngoài
  function handleClickOutside(event) {
    if (popupRef && !popupRef.contains(event.target) && !buttonRef.contains(event.target)) {
      showPopup = false;
    }
  }

  onMount(async () => {
    document.addEventListener('click', handleClickOutside);
  });
  // Đừng quên dọn dẹp event
  onDestroy(() => {
    document.removeEventListener('click', handleClickOutside);
  });

  export let content;
  let menuItems = [
    {
      name: 'addItem',
      onClick: () => {
        value = { addItem: true };
      },
      displayText: 'Add Item',
      class: '➕'
    },
    {
      name: 'emptyicons',
      onClick: () => (content.textContent = 'Empty Icon...'),
      displayText: 'Empty Icon',
      class: '⬜'
    },
    {
      name: 'zoom',
      onClick: () => (content.textContent = 'Zoom...'),
      displayText: 'Zoom',
      class: '🔍'
    },
    {
      name: 'printMenu',
      onClick: () => {
        value = { print: true };
      },
      displayText: 'Print',
      class: '🖨️'
    },
    { name: 'hr' },
    {
      name: 'settings',
      onClick: () => (content.textContent = 'settings...'),
      displayText: 'Settings',
      class: '⚙️'
    },
    { name: 'hr' },
    {
      name: 'showPanel',
      onClick: () => {
        value = { showPanel: true };
      },
      displayText: 'Show Panel',
      class: '🎦'
    },
    {
      name: 'hidePanel',
      onClick: () => {
        value = { hidePanel: true };
      },
      displayText: 'Hide Panel',
      class: '💤'
    },
    { name: 'hr' },
    {
      name: 'trash',
      onClick: () => (content.textContent = 'Removed...'),
      displayText: 'Trash',
      class: '🗑️'
    },
    {
      name: 'once-layout',
      onClick: () => {
        if (LayoutK2?.importLayoutConfig) {
          const layout = {
            'layout__2k-body': [
              {
                min: 0,
                max: 100,
                size: 19,
                snap: 9
              },
              {
                min: 0,
                max: 100,
                size: 67,
                snap: 34
              },
              {
                min: 0,
                max: 100,
                size: 14,
                snap: 7
              }
            ],
            'layout__2k-main': [
              {
                min: 0,
                max: 100,
                size: 5,
                snap: 3
              },
              {
                min: 0,
                max: 100,
                size: 90,
                snap: 45
              },
              {
                min: 0,
                max: 100,
                size: 5,
                snap: 2
              }
            ],
            'layout__2k-content': [
              {
                min: 0,
                max: 100,
                size: 10,
                snap: 5
              },
              {
                min: 0,
                max: 100,
                size: 84,
                snap: 42
              },
              {
                min: 0,
                max: 100,
                size: 6,
                snap: 3
              }
            ]
          };
          LayoutK2.importLayoutConfig(layout);
        }
      },
      displayText: 'Once Layout',
      class: '🎨'
    },
    {
      name: 'export-layout',
      onClick: () => {
        if (LayoutK2?.exportLayoutConfig) {
          const ex = LayoutK2.exportLayoutConfig();
          //console.log('layout::', ex);
          download(JSON.stringify(ex, null, 2), 'layout.backup', 'application/json');
        }
      },
      displayText: 'Export Layout',
      class: '🔧'
    },
    {
      name: 'import-layout',
      onClick: () => {
        value = { impLayout: true };
      },
      displayText: 'Import Layout',
      class: '🔐 '
    },
    {
      name: 'reset-layout',
      onClick: () => {
        if (LayoutK2?.resetLayoutConfig) {
          LayoutK2.resetLayoutConfig();
        }
      },
      displayText: 'Reset Layout',
      class: '🚪'
    }
  ];

  $: changeConten(content);
  const changeConten = (content) => {
    if (content?.code === 'impLayout') {
      let data = JSON.parse(content.data);
      if (LayoutK2?.importLayoutConfig) LayoutK2.importLayoutConfig(data);
    }
  };
</script>

<button bind:this={buttonRef} on:click={togglePopup}>
  <Icon name="monitor-cog" class="text-red-500 hover:opacity-80" width={24} height={24} />
</button>

<Portal>
  {#if showPopup}
    <nav
      bind:this={popupRef}
      style={popupStyle}
      class="absolute z-50 bg-white border border-gray-300 rounded-md shadow-lg w-56"
      on:click|stopPropagation>
      <ul class="p-2">
        {#each menuItems as item}
          {#if item.name === 'hr'}
            <hr class="my-2 border-t border-gray-200" />
          {:else}
            <li>
              <button
                class="w-full text-left px-3 py-2 bg-white hover:bg-gray-100 rounded-md flex items-center gap-2 text-sm text-gray-800"
                on:click={() => {
                  item.onClick();
                  showPopup = false;
                }}>
                <span>{item.class}</span>
                {item.displayText}
              </button>
            </li>
          {/if}
        {/each}
      </ul>
    </nav>
  {/if}
</Portal>

