<script>
  import { onMount } from 'svelte';
  import { Splitpanes } from 'svelte-splitpanes';
  // import "../../../styles/splitpanes.css";

  export let key = '';
  export let horizontal = false;
  export let defaultSizes = [];
  export let prefix = 'layout__';

  let paneSizes = [];
  let _key = `${prefix}${key}`;

  onMount(() => {
    const saved = localStorage.getItem(_key);
    if (saved) {
      try {
        paneSizes = JSON.parse(saved);
      } catch {
        paneSizes = defaultSizes;
      }
    } else {
      paneSizes = defaultSizes;
    }
  });

  const handleResize = (event) => {
    const sizes = event.detail;
    const result =
      Array.isArray(sizes) &&
      sizes?.map((item) => ({
        ...item,
        size: Number.isFinite(item.size) ? Math.round(item.size) : item.size,
        snapSize: Math.round(item.size / 2)
      }));
    //console.log('result', result);
    localStorage.setItem(_key, JSON.stringify(result));
  };
</script>

<Splitpanes {horizontal} on:resize={handleResize}>
  <slot {paneSizes} />
</Splitpanes>
