<script>
  import { onMount } from 'svelte';
  import Layout from '../components/Layout.svelte';
  // import CamView from '@components/CamView.svelte';
  import Worklist from './worklist/index.svelte';
  import History from './histories/index.svelte';
  import CameraSetting from '@components/CameraSettings.svelte';
  import Search from './search/index.svelte';
  import Board from '@components/dnd/Broad.svelte';
  import Item from '@components/dnd/ItemDnd.svelte';
  import Tabs from '@components/tabs/vscode.svelte';
  import TabDetails from '@components/tabs/details/TabDetails.svelte';
  import Icon from '@components/Icon.svelte';

  import Settings from '@components/ui/context-menu/Settings.svelte';
  import Modal from '@components/ui/modal/Modal.svelte';
  import FileImporter from '@components/ui/files/FileImporter.svelte';
  import { writable } from 'svelte/store';

  import ApiClient from '$lib/api';
  let items = [];
  let loaded = false;
  let total = 0;
  $: id = undefined;
  // let page = 1;
  // let limit = 20;
  // let keyword = '';

  const api = new ApiClient();

  // Hàm fetch data
  async function fetchData({ keyword = '', page = 0, limit = 0 } = {}) {
    //console.clear();
    console.log('Fetching data with:::', keyword, page, limit);
    loaded = false;
    try {
      const json = await api.get(
        `/api/endo/results?limit=${limit}&page=${page}&keyword=${encodeURIComponent(keyword)}`
      );
      if (json.success) {
        items = json.data;
        total = json?.metadata?.total || 0;
        //console.log('Items loaded:', json.metadata.total, items);
      } else {
        console.error('API error:', json.error);
      }
    } catch (err) {
      console.error('Fetch error:', err);
    } finally {
      loaded = true;
    }
  }

  // lần đầu load
  onMount(() => {
    fetchData();
  });

  // lắng nghe event từ Search
  function handleSearch(e) {
    fetchData({ ...e.detail });
  }

  // lắng nghe event đổi trang
  function handlePageChange(e) {
    console.log('Page change event:', e.detail);
    fetchData({ ...e.detail });
  }

  let settings = undefined;
  let setting = undefined;
  let open = false;
  let modalOption = { width: '80%', height: '80%', title: '' };

  let isOpen = false;

  let option = {};

  $: changeSettings(settings);

  const changeSettings = (settings) => {
    if (settings?.print) {
      open = true;
    } else if (settings?.addItem) {
      open = true;
    } else if (settings?.impLayout) {
      open = true;
      modalOption = { width: '60%', height: '60%', title: 'Nhập liệu giao diện' };
    } else if (settings?.showPanel) {
      option.showBodyRight = true;
    } else if (settings?.hidePanel) {
      option.showBodyRight = false;
    }
  };

  let activeTab;
  //$: console.log('activeTab', activeTab);

  let importedContent = {};

  function handleImport(data, file) {
    importedContent.data = data;
    importedContent.code = 'impLayout';
    importedContent.fileName = file.name;
  }

  let board = [
    {
      id: 1,
      name: 'TODO',
      items: [
        { id: 41, name: 'item41' },
        { id: 42, name: 'item42' },
        { id: 43, name: 'item43' },
        { id: 44, name: 'item44' },
        { id: 45, name: 'item45' },
        { id: 46, name: 'item46' },
        { id: 47, name: 'item47' },
        { id: 48, name: 'item48' },
        { id: 49, name: 'item49' }
      ]
    },
    {
      id: 2,
      name: 'DOING',
      items: []
    },
    {
      id: 3,
      name: 'DONE',
      items: []
    }
  ];

  // const onSelectItem = (e) => {
  //   const item = e.detail || {};
  //   //console.log('Item clicked:', e.detail);
  //   activeTab = { type: 'result', label: 'Kết quả ' + item.code };
  //   id = item.id_iuid;
  // };

  let tabs = [{ id: '1', title: 'Home', kind: 'home', data: { id: '' } }];
  let activeTabId = null;

  function openTab(item) {
    const exists = tabs.find((t) => t.kind === item.kind);
    if (!exists) {
      tabs = [
        ...tabs,
        {
          id: crypto.randomUUID(),
          title: item.title || 'Kết quả',
          kind: item.kind || 'result',
          data: item || {}
        }
      ];
    }
    activeTabId = item.kind || tabs[tabs.length - 1].kind;
    id = item.id_iuid || tabs[tabs.length - 1].data.id;
  }

  function closeTab(kind) {
    tabs = tabs.filter((t) => t.kind !== kind);
    if (activeTabId === kind) {
      activeTabId = tabs.length ? tabs[tabs.length - 1].kind : null;
    }
  }

  $: console.log('Tabs->', tabs, activeTabId);
</script>

<Modal
  {open}
  onClose={() => {
    open = false;
  }}
  {...modalOption}>
  {#if settings?.print}
    <iframe
      src="https://demo.evat.vn/temp/20250517/15/9991_dd8bba67a89443e280dd88366563933b/C25TGT_00000138.pdf?zoom=250&panel=hide"
      class="w-full h-full">
    </iframe>
  {:else if settings?.addItem}
    <div class="w-full h-full flex flex-col items-center justify-center">
      <h2>Nội dung bất kỳ ở đây</h2>
      <p>Bạn có thể đặt bất cứ gì vào slot!</p>
    </div>
  {:else if settings?.impLayout}
    <FileImporter label="Tải file lên" onImport={handleImport} accept={'.txt,.json,.csv,.backup'} />
    {#if importedContent?.data}
      <div class="mt-4 p-4 border rounded bg-gray-100">
        <p class="font-bold">📄 {importedContent?.fileName}</p>
        <pre class="whitespace-pre-wrap">{importedContent?.data}</pre>
      </div>
    {/if}
  {/if}
</Modal>

<Layout {option}>
  <svelte:fragment slot="content">
    <TabDetails
      {tabs}
      {activeTabId}
      {id}
      let:tabs
      let:activeTabId
      let:isLoading
      let:currentComponent
      let:tab
      let:error
      let:id>
      {#if isLoading}
        <div class="p-4 text-gray-400 italic">⏳ Loading tab content...</div>
      {:else if currentComponent}
        <svelte:component this={currentComponent} {tabs} {activeTabId} {id} />
      {:else if error}
        <div class="p-4 text-red-500 italic">❌ {error.message}</div>
      {/if}
    </TabDetails>
  </svelte:fragment>
  <svelte:fragment slot="left">
    <div class="min-w-[330px] w-full h-full flex flex-col">
      <!-- Search section -->
      <div class="min-h-[280px] bg-gray-200">
        <Search
          on:search={handleSearch}
          on:pageChange={handlePageChange}
          on:addUser={() => console.log('Add user')}
          on:addGroup={() => console.log('Add group')}
          on:tabChange={() => console.log('Tab change')}
          {total} />
      </div>

      <!-- Worklist section -->
      <div class="flex-1 bg-gray-100 overflow-auto">
        <Worklist {items} {loaded} on:click={(e) => openTab({ ...e.detail, kind: 'result' })} />
      </div>
    </div>
  </svelte:fragment>

  <svelte:fragment slot="head">
    <!-- <Tabs bind:tab={activeTab} /> -->
    <Tabs {tabs} bind:activeTabId on:closeTab={(e) => closeTab(e.detail)} />
  </svelte:fragment>

  <svelte:fragment slot="header">
    <div class="flex justify-between flex-row items-center w-full">
      <div></div>
      <div></div>
      <div>
        <Settings bind:value={settings} content={importedContent} />
      </div>
    </div>
  </svelte:fragment>

  <svelte:fragment slot="right">
    <History />
  </svelte:fragment>

  <svelte:fragment slot="comment">comment</svelte:fragment>
</Layout>
