export { default as K2Split } from './K2Split.svelte';
export { default as K2Pane } from './K2Pane.svelte';
let prefix = 'layout__';

export const exportLayoutConfig = () => {
    const exported = {};

    for (const key in localStorage) {
        if (key.startsWith(prefix)) {
            exported[key] = JSON.parse(localStorage.getItem(key));
        }
    }

    // console.log('----*-----');
    // for (const key in exported) {
    //     console.log(key, ":", exported[key]);
    // }

    return exported;
};

export const importLayoutConfig = (data) => {
    for (const key in data) {
        if (key.startsWith(prefix)) {
            localStorage.setItem(key, JSON.stringify(data[key]));
        }
    }
    setTimeout(() => {
        window.location.reload();
    }, 1000);
};

export const resetLayoutConfig = () => {
    const keysToRemove = [];

    for (const key in localStorage) {
        if (key.startsWith(prefix)) {
            keysToRemove.push(key);
        }
    }

    keysToRemove.forEach((key) => localStorage.removeItem(key));
    setTimeout(() => {
        window.location.reload();
    }, 1000);
};

export default {
    exportLayoutConfig,
    importLayoutConfig,
    resetLayoutConfig,
};