<script>
  export let label = 'Import File';
  export let accept = '.txt,.json,.csv'; // Cho phép mở rộng
  export let onImport = (text, file) => {}; // Callback từ ngoài truyền vào

  let fileInput;

  function handleClick() {
    fileInput.value = ''; // Xóa để có thể chọn lại cùng 1 file
    fileInput.click();
  }

  function handleChange(event) {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();

    reader.onload = (e) => {
      const content = e.target.result;
      onImport(content, file); // Gọi callback với nội dung file và object file
    };

    reader.onerror = () => {
      console.error('Lỗi khi đọc file');
    };

    reader.readAsText(file);
  }
</script>

<!-- Nút import -->
<button on:click={handleClick} class="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition">
  {label}
</button>

<!-- Input ẩn -->
<input bind:this={fileInput} type="file" {accept} on:change={handleChange} class="hidden" />
