<script>
  import { dndzone } from 'svelte-dnd-action';
  import { onMount } from 'svelte';

  export let tabs = [];
  export let activeTabId;
  export let theme = 'light';
  let tabCounter = tabs.length || 1;

  import { createEventDispatcher } from 'svelte';
  const dispatch = createEventDispatcher();

  function setActive(id) {
    activeTabId = id;
  }

  function doClose(id) {
    dispatch('closeTab', id);
  }

  function handleDnd({ detail }) {
    tabs = detail.items;
  }

  function newTab() {
    tabCounter += 1;
    const newTab = { id: tabCounter, title: `Tab ${tabCounter}`, kind: 'new' + tabCounter, data: {} };
    tabs = [...tabs, newTab];
    activeTabId = newTab.kind;
  }

  onMount(() => {
    if (tabs.length > 0) {
      activeTabId = tabs[0].kind;
    }
  });
</script>

<div
  class="tabs-container flex items-center text-sm h-10 px-2 overflow-x-auto border-b"
  class:bg-gray-100={theme === 'light'}
  class:bg-[#1e1e1e]={theme === 'dark'}
  class:border-gray-300={theme === 'light'}
  class:border-[#2a2a2a]={theme === 'dark'}
  use:dndzone={{ items: tabs, flipDurationMs: 150 }}
  on:consider={handleDnd}
  on:finalize={handleDnd}>
  {#each tabs as tab (tab.id)}
    <div
      class="tab relative flex items-center px-4 h-full cursor-pointer select-none transition group tab-layout"
      class:text-gray-700={theme === 'light'}
      class:text-gray-300={theme === 'dark'}
      class:hover:text-black={theme === 'light'}
      class:hover:text-white={theme === 'dark'}
      class:border-b-2={tab.kind === activeTabId}
      class:border-blue-500={tab.kind === activeTabId}
      class:bg-white={theme === 'light' && tab.kind === activeTabId}
      class:bg-[#2d2d2d]={theme === 'dark' && tab.kind === activeTabId}
      on:click={() => setActive(tab.kind)}>
      <span>{tab?.title || ''}</span>
      <span
        class="close ml-1.5 w-5 h-5 text-sm rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all duration-200 ease-in-out cursor-pointer"
        class:text-red-500={theme === 'light'}
        class:text-red-400={theme === 'dark'}
        class:hover:bg-gray-200={theme === 'light'}
        class:hover:bg-[#444]={theme === 'dark'}
        on:click|stopPropagation={() => doClose(tab.kind)}>
        ×
      </span>
    </div>
  {/each}

  <div
    class="ml-2 cursor-pointer text-lg px-2 transition"
    class:text-gray-500={theme === 'light'}
    class:hover:text-black={theme === 'light'}
    class:text-gray-400={theme === 'dark'}
    class:hover:text-white={theme === 'dark'}
    on:click={newTab}>
    +
  </div>
</div>
