import { tick } from "svelte";

const componentCache = new Map();
const componentRegistry = new Map();

/**
 * Đăng ký loader động cho 1 component
 */
export function registerComponentByName(name, loader) {
    componentRegistry.set(name, loader);
}

/**
 * Auto-register toàn bộ component từ 1 folder
 * @param {string}  
 */
export function autoRegister() {
    // Thay đường dẫn này cho đúng với project của bạn @components/**/*.svelte
    const modules = import.meta.glob('@components/tabs/details/*.svelte');
    //www/src/components/tabs/details
    for (const [path, loader] of Object.entries(modules)) {
        const name = path.split('/').pop().replace('.svelte', '');
        //console.log(`Auto-register component: ${name} from ${path}`);
        if (!name) continue;
        registerComponentByName(name, loader);
    }
}

/**
 * Load component theo tên, có cache
 */
export async function loadComponentByName(name) {
    if (!name) return null;
    // nếu có trong cache thì lấy luôn
    if (componentCache.has(name)) {
        return componentCache.get(name);
    }
    // lấy loader từ registry
    const loader = componentRegistry.get(name);
    if (!loader) {
        console.warn(`⚠️ Component ${name} chưa được register`);
        return null;
    }
    const { default: component } = await loader();
    componentCache.set(name, component);
    return component;
}

export async function preloadComponents(names = []) {
    for (const name of names) {
        try {
            await loadComponentByName(name);
        } catch (err) {
            console.error(`❌ Lỗi preload ${name}:`, err);
        }
    }
    //console.log('✅ Preload components done:', names);
}

export function registerComponent(type, loaderFn) {
    if (!type || typeof type !== "string") {
        console.error("❌ registerComponent: type không hợp lệ:", type);
        return false;
    }
    if (typeof loaderFn !== "function") {
        console.error(`❌ registerComponent: loaderFn cho "${type}" không phải function`, loaderFn);
        return false;
    }
    if (componentRegistry.has(type)) {
        console.warn(`⚠️ registerComponent: Component "${type}" đã tồn tại, sẽ ghi đè.`);
    }

    componentRegistry.set(type, loaderFn);
    console.log(`✅ Đăng ký component: "${type}" thành công.`);
    return true;
}

/**
 * Xoá component khỏi registry
 * @param {string} type - Tên component
 */
export function unregisterComponent(type) {
    if (!type || typeof type !== "string") {
        console.error("❌ unregisterComponent: type không hợp lệ:", type);
        return false;
    }
    if (!componentRegistry.has(type)) {
        console.warn(`⚠️ unregisterComponent: Không tìm thấy component "${type}" trong registry.`);
        return false;
    }

    componentRegistry.delete(type);
    console.log(`🗑️ Đã xoá component: "${type}".`);
    return true;
}

/**
 * Lấy loaderFn theo tên component
 * @param {string} type - Tên component
 * @returns {Function|null} loaderFn hoặc null nếu không có
 */
export function getComponent(type) {
    if (!type || typeof type !== "string") {
        console.error("❌ getComponent: type không hợp lệ:", type);
        return null;
    }
    if (!componentRegistry.has(type)) {
        console.warn(`⚠️ getComponent: Không tìm thấy component "${type}" trong registry.`);
        return null;
    }
    return componentRegistry.get(type);
}
