<script>
  import {
    Search,
    UserPlus,
    Users,
    ChevronLeft,
    ChevronRight,
    ChevronsLeft,
    ChevronsRight,
    ChevronDown
  } from 'lucide-svelte';
  import { createEventDispatcher } from 'svelte';

  const dispatch = createEventDispatcher();

  let keyword = '';
  let page = 1;
  export let total = 0;
  const pageSizeOptions = [1, 3, 5, 10, 20, 50, 100, 500];
  let pageSize = pageSizeOptions[2]; // default 5
  let showDropdown = false;
  let customSize = '';

  $: totalPages = pageSize === 0 ? 1 : Math.ceil(total / pageSize);

  function changePage(newPage) {
    if (newPage < 1 || newPage > totalPages) return;
    page = newPage;
    dispatch('pageChange', { keyword, page, limit: pageSize });
  }

  // function changePageSize(e) {
  //   pageSize = parseInt(e.target.value);
  //   page = 1; // reset về trang đầu khi đổi pageSize
  //   dispatch('pageChange', { keyword, page, limit: pageSize });
  // }

  function apply(size) {
    if (isNaN(size) || size < 0) return;

    pageSize = size;
    customSize = '';
    showDropdown = false;
    dispatch('search', { keyword, page, limit: pageSize });
  }

  function toggle() {
    showDropdown = !showDropdown;
  }

  function doSearch() {
    dispatch('search', { keyword, page, limit: pageSize });
  }

  // function changePage(newPage) {
  //   if (newPage < 1) return;
  //   page = newPage;
  //   dispatch('pageChange', { page });
  // }

  function addUser() {
    dispatch('addUser');
  }

  function addGroup() {
    dispatch('addGroup');
  }

  function tabChange(tab) {
    dispatch('tabChange', { tab });
  }
</script>

<div class="bg-white border-b shadow p-4 mt-0 flex flex-col gap-4">
  <!-- Hàng 1: Search box + nút -->
  <div class="flex items-center gap-2">
    <div class="relative flex-1">
      <Search class="absolute left-2 top-1/2 -translate-y-1/2 text-gray-400" size={18} />
      <input
        type="text"
        bind:value={keyword}
        placeholder="Tìm kiếm..."
        class="w-full h-10 pl-8 pr-3 border rounded-md focus:outline-none focus:ring"
        on:keydown={(e) => e.key === 'Enter' && doSearch()} />
    </div>

    <button
      class="w-10 h-10 flex items-center justify-center bg-transparent text-gray-400 rounded-md hover:bg-gray-100"
      title="Tìm"
      on:click={doSearch}>
      <Search size={20} />
    </button>

    <button
      class="w-10 h-10 flex items-center justify-center bg-transparent text-gray-400 rounded-md hover:bg-gray-100"
      title="Thêm user"
      on:click={addUser}>
      <UserPlus size={20} />
    </button>

    <button
      class="w-10 h-10 flex items-center justify-center bg-transparent text-gray-400 rounded-md hover:bg-gray-100"
      title="Thêm nhóm"
      on:click={addGroup}>
      <Users size={20} />
    </button>
  </div>

  <!-- Hàng 2: Toggle nâng cao -->
  <details class="border rounded-md">
    <summary class="cursor-pointer px-3 py-2">⚙️ Tùy chọn nâng cao</summary>
    <div class="p-3 text-sm text-gray-700">
      <p>Lọc theo loại, trạng thái, ngày tạo…</p>
    </div>
  </details>

  <!-- Hàng 3: Phân trang -->
  <div class="flex flex-col items-center gap-2 text-sm py-3 border-t">
    <!-- Dòng 1: Thông tin -->
    <div class="text-gray-600">
      {#if pageSize === 0}
        Hiển thị tất cả {total} mục
      {:else}
        Hiển thị {(page - 1) * pageSize + 1}–
        {Math.min(page * pageSize, total)} trong {total}
      {/if}
    </div>

    <!-- Dòng 2: Pagination -->
    <div class="flex items-center gap-2 flex-nowrap">
      <!-- Điều hướng -->
      <div class="flex items-center gap-1 flex-nowrap">
        <button
          class="p-1 border rounded hover:bg-gray-100 disabled:opacity-50"
          disabled={page === 1}
          on:click={() => changePage(1)}>
          <ChevronsLeft size={14} />
        </button>

        <button
          class="p-1 border rounded hover:bg-gray-100 disabled:opacity-50"
          disabled={page === 1}
          on:click={() => changePage(page - 1)}>
          <ChevronLeft size={14} />
        </button>

        <!-- Trang số (tối đa 3 trang gần nhất) -->
        {#each Array(totalPages) as _, i (i)}
          {#if Math.abs(i + 1 - page) <= 1}
            <button
              class="px-2 py-1 border rounded text-sm hover:bg-gray-100
                   {page === i + 1 ? 'bg-blue-600 text-white border-blue-600' : ''}"
              on:click={() => changePage(i + 1)}>
              {i + 1}
            </button>
          {/if}
        {/each}

        <button
          class="p-1 border rounded hover:bg-gray-100 disabled:opacity-50"
          disabled={page === totalPages}
          on:click={() => changePage(page + 1)}>
          <ChevronRight size={14} />
        </button>

        <button
          class="p-1 border rounded hover:bg-gray-100 disabled:opacity-50"
          disabled={page === totalPages}
          on:click={() => changePage(totalPages)}>
          <ChevronsRight size={14} />
        </button>
      </div>

      <div class="relative inline-block text-sm">
        <!-- Nút chính -->
        <button
          class="flex items-center gap-1 border rounded-lg px-3 py-1.5 bg-white shadow-sm hover:bg-gray-50"
          on:click={toggle}>
          [{pageSize === 0 ? 'All' : pageSize}]
          <ChevronDown size={16} class="text-gray-500 transition-transform {showDropdown ? 'rotate-180' : ''}" />
        </button>

        <!-- Dropdown -->
        {#if showDropdown}
          <div
            class="absolute mt-1 w-25 rounded-lg border border-gray-200 bg-white shadow-lg ring-1 ring-black/50 z-10 animate-fadeIn">
            <!-- Options -->
            {#each pageSizeOptions as size}
              <button
                class="w-full text-left px-3 py-2 hover:bg-blue-50 hover:text-blue-600 transition-colors"
                on:click={() => apply(size)}>
                {size}
              </button>
            {/each}

            <!-- Custom input -->
            <div class="border-t border-b-emerald-900 px-3 py-2 flex items-center gap-2">
              <input
                type="number"
                min="0"
                bind:value={customSize}
                placeholder="0"
                class="w-16 border rounded px-2 py-1 text-xs focus:ring focus:border-blue-500"
                on:keydown={(e) => e.key === 'Enter' && apply(parseInt(customSize))} />
              <button
                class="px-2 py-1 text-xs rounded bg-blue-600 text-white hover:bg-blue-700 hidden"
                on:click={() => apply(parseInt(customSize))}>
                OK
              </button>
            </div>
          </div>
        {/if}
      </div>
    </div>
  </div>

  <!-- Hàng 4: Tabs -->
  <div class="flex gap-2 border-b text-sm">
    {#each ['Tất cả', 'Chưa đọc', 'Đã ghim', 'Nhóm'] as tab}
      <button
        class="px-4 py-2 hover:text-blue-500 border-b-2 {tab === 'Tất cả'
          ? 'border-blue-500 font-semibold'
          : 'border-transparent'}"
        on:click={() => tabChange(tab)}>
        {tab}
      </button>
    {/each}
  </div>
</div>

<style>
  /* Animation nhỏ */
  .animate-fadeIn {
    animation: fadeIn 0.15s ease-out;
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-4px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
</style>
